.is-pc {
  display: block;
}
@media (max-width: 768px) {
  .is-pc {
    display: none;
  }
}

.is-sp {
  display: none;
}
@media (max-width: 768px) {
  .is-sp {
    display: block;
  }
}

.is-tab {
  display: none;
}
@media (max-width: 1100px) {
  .is-tab {
    display: block;
  }
}

.is-only-tab {
  display: none;
}
@media (min-width: 768px) and (max-width: 1200px) {
  .is-only-tab {
    display: block;
  }
}

.is-only-pc {
  display: none;
}
@media (min-width: 1200px) {
  .is-only-pc {
    display: block;
  }
}

* {
  box-sizing: border-box;
}
*:before, *:after {
  box-sizing: inherit;
}

html,
body {
  text-size-adjust: 100%;
}

html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.6rem;
  line-height: 1;
  color: #bbbaa6;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

a {
  color: #bbbaa6;
  text-decoration: none;
}

p {
  margin: 0;
}

img {
  width: 100%;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

dl,
dt,
dd {
  padding: 0;
  margin: 0;
}

.mv {
  width: 100%;
  height: 100dvh;
}
.mv-wrap {
  contain: content;
  background-color: #0e100f;
}
.mv-inner {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, min-content);
}
.mv-txt {
  display: grid;
  height: 100dvh;
  gap: 5px;
  grid-template-columns: repeat(12, min-content);
  justify-content: center;
}
.mv-txt__item {
  display: flex;
  align-items: center;
  font-size: 80px;
  color: #fff;
}
.mv-scroll__down {
  position: absolute;
  width: fit-content;
  height: fit-content;
  inset: 0;
  margin: auto;
  color: #fff;
  font-size: 64px;
}

#top {
  background-color: #0e100f;
}
#top .main-content {
  width: 100%;
  margin-bottom: 300px;
}
#top .main-content__wrap {
  display: grid;
  gap: 30px;
  width: 80%;
  margin: 0 auto;
  line-height: 1.5;
}
#top .overlap-ttl {
  display: flex;
  width: 100%;
  height: 100dvh;
  justify-content: center;
  align-items: center;
  font-size: 40px;
}
#top .overlap-layout {
  position: relative;
  width: 100%;
  height: 100dvh;
}
#top .overlap-layout__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100dvw;
  height: 100dvh;
}
#top .overlap-layout__item[data-img="01"] {
  background: url("/assets/img/overlap-bg01.jpg") left top/cover no-repeat;
}
#top .overlap-layout__item[data-img="02"] {
  background: url("/assets/img/overlap-bg02.jpg") left top/cover no-repeat;
}
#top .overlap-layout__item[data-img="03"] {
  background: url("/assets/img/overlap-bg03.jpg") left top/cover no-repeat;
}
#top .overlap-layout__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
#top .overlap-layout__wrap p {
  color: #fff;
  font-size: 50px;
}